import { withRouter } from 'react-router-dom';

import CryptoJS from 'crypto-js';
import sha256 from 'crypto-js/sha256';

import HDWalletProvider from '@truffle/hdwallet-provider';


declare var $ : any;

const Web3 = require('web3');

